#projects{
    padding: 30px 0;
    background-color:#cdc1a3;
}

.projects__link {
    position: relative;
    &:hover .overlay {
     opacity: 1;
    }   
}
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.91);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:5px;

    padding: 40px;
    color: #fff;
    opacity: 0;
    top: 0;

    transition: opacity 400ms linear;
    @media screen and (max-width:1200px){
        padding: 10px;
    }
}

.projects{
    @media screen and (min-width:768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 50px;
    }
}
.project-title {
    padding: 5px 15px;
    border: 1px solid #fff;
    border-radius: 33px;
    font-size: 12px;
    @media screen and (min-width:768px) {
    font-size: 14px;
    padding: 10px 30px;
        }
}

.projects_list{
    display: flex;
    flex-direction: column;
    gap: 20px;

    padding: 0 50px;
    @media screen and (min-width:768px) and (max-width:1200px) {
            padding: 0;
        }
     li{
        // height: 260px;
        overflow: hidden;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;

        &:hover{
            transform: scale(1.03);
        }
       
    }
}

.projects__title{
    text-align: center;
    margin-bottom: 20px;
    
}
.text.text_project{
    font-size: 8px;
    @media screen and (min-width:768px) {
        font-size: 14px;
    }
}
#team_projects{
    @media  screen and (max-width:768px) {
        margin-bottom: 20px;
    }
    
}